.blink {
    -webkit-animation: 2s blink infinite;
            animation: 2s blink infinite;
    opacity: 0.1;
}

@-webkit-keyframes blink{
    0% {
        opacity: 0.1;
    }
    70% {
        opacity: 1;
    }
}

@keyframes blink{
    0% {
        opacity: 0.1;
    }
    70% {
        opacity: 1;
    }
}


