.App {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.block {
  width: 30%;
  height: 100vh;
  border: 1px solid white;
}

.fade-black {
  background-color: black;
  -webkit-animation: 2s fading;
          animation: 2s fading;
}

.fade-blue {
  background-color: blue;
  -webkit-animation: 2s fading;
          animation: 2s fading;
}

.fade-red {
  background-color: red;
  -webkit-animation: 2s fading;
          animation: 2s fading;
}

.fade-yellow {
  background-color: yellow;
  -webkit-animation: 2s fading;
          animation: 2s fading;
}

@-webkit-keyframes fading {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fading {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


